import delay from './delay';

// This file mocks a web API by working with the hard-coded data below.
// It uses setTimeout to simulate the delay of an AJAX call.
// All calls return promises.
const authors = [
	{
		id: 'cory-house',
		firstName: 'Cory',
		lastName: 'House'
	},
	{
		id: 'scott-allen',
		firstName: 'Scott',
		lastName: 'Allen'
	},
	{
		id: 'dan-wahlin',
		firstName: 'Dan',
		lastName: 'Wahlin'
	},
	{
		id: 'sam-ewdala',
		firstName: 'Sam',
		lastName: 'Ewdala'
	}
];

//This would be performed on the server in a real app. Just stubbing in.
const generateId = (author) => `${author.firstName.toLowerCase()} - ${author.lastName.toLowerCase()}`;

class AuthorApi {
	static getAllAuthors() {
		return new Promise((resolve, reject) => {
			console.log(reject);
			setTimeout(() => {
				resolve(Object.assign([], authors));
			}, delay);
		});
	}

	static saveAuthor(author) {
		const authorObj = Object.assign({}, author); // to avoid manipulating object passed in.
		return new Promise((resolve, reject) => {
			setTimeout(() => {
				// Simulate server-side validation
				const minAuthorNameLength = 3;
				if (authorObj.firstName.length < minAuthorNameLength) {
					reject(`First Name must be at least ${minAuthorNameLength} characters.`);
				}

				if (authorObj.lastName.length < minAuthorNameLength) {
					reject(`Last Name must be at least ${minAuthorNameLength} characters.`);
				}

				if (authorObj.id) {
					const existingAuthorIndex = authors.findIndex(a => a.id === authorObj.id);
					authors.splice(existingAuthorIndex, 1, authorObj);
				} else {
					//Just simulating creation here.
					//The server would generate ids for new authors in a real app.
					//Cloning so copy returned is passed by value rather than by reference.
					authorObj.id = generateId(authorObj);
					authors.push(authorObj);
				}

				resolve(authorObj);
			}, delay);
		});
	}

	static deleteAuthor(authorId) {
		return new Promise((resolve) => {
			setTimeout(() => {
				const indexOfAuthorToDelete = authors.findIndex(author => author.id === authorId);
				authors.splice(indexOfAuthorToDelete, 1);
				resolve();
			}, delay);
		});
	}
}

export default AuthorApi;



